@import '../../scss';

:host {
  .button-container {
    display: flex;

    @include pds-media-query-min-max('base', 'xs') {
      flex-direction: column;
      justify-content: space-between;
    }

    @include pds-media-query-min-max('s', 'l') {
      flex-direction: column;
      justify-content: space-between;
    }

    .login {
      padding: p-px-to-rem(36px) 0 p-px-to-rem(16px) 0;

      @include pds-media-query-min-max('base', 's') {
        width: 100%;
        padding-top: 0;
      }

      @include pds-media-query-min('xs') {
        padding: 0 p-px-to-rem(8px) p-px-to-rem(16px) 0;
      }

      @include pds-media-query-min-max('s', 'l') {
        padding: 0 0 p-px-to-rem(16px) 0;
      }

      @include pds-media-query-min('xl') {
        padding: 0 p-px-to-rem(16px) p-px-to-rem(23px) 0;
      }
    }

    .more {
      padding: 0 0 p-px-to-rem(16px) 0;

      @include pds-media-query-min-max('base', 's') {
        width: 100%;
      }

      @include pds-media-query-min('xs') {
        padding: 0 0 p-px-to-rem(16px) p-px-to-rem(8px);
      }

      @include pds-media-query-min('s') {
        padding: 0 0 p-px-to-rem(23px) 0
      }

      @include pds-media-query-min('xl') {
        padding: 0 p-px-to-rem(16px) p-px-to-rem(23px) 0;
      }
    }
  }

  phn-p-text {
    padding-bottom: p-px-to-rem(24px);
    border-bottom: 2px solid $pds-theme-light-contrast-low;
    margin-bottom: p-px-to-rem(10px);

    @include pds-media-query-min('xxl') {
      margin-bottom: p-px-to-rem(16px);
    }
  }

  phn-p-link-pure {
    float: left;
    text-decoration: underline;
    color: black;
  }
}
