import { Component, Event, EventEmitter, Fragment, h, JSX, Prop } from '@stencil/core';
import { TLink } from '../../entities/content';
import globalDataLayerService from '../../services/analytics/global-data-layer-service';
import { ClickElementTypes, ElementTypes, EventActions } from '../../utility/constants';
import { isString } from '../../utility/helper';
import analyticsManager from '../../services/analytics/analytics-manager';
import { RoutingKeys } from '../../entities/routing-key';

@Component({
  tag: 'phn-myporsche-login',
  styleUrl: 'myporsche-login.scss',
  scoped: true
})
export class MyPorscheLogin {
  @Prop() login!: TLink;
  @Prop() more: TLink | null = null;
  @Prop() signup!: TLink;
  @Prop() signupText!: string;

  @Event() loginEvent!: EventEmitter<void>;

  private sendDataLayerEvent(link: TLink | null): void {
    globalDataLayerService.pushDataLayerEvent(EventActions.NAVIGATION_LINK_CLICK, {
      componentClick: {
        clickElementId: link?.id,
        clickElementName: link?.text,
        clickElementType: ClickElementTypes.Link,
        targetUrl: link?.link
      },
      componentDisplay: {
        displayElementId: analyticsManager.calculateDisplayElementId(),
        displayElementType: analyticsManager.calculateDisplayElementType(
          RoutingKeys.MY_PORSCHE,
          ElementTypes.myPorscheDrawer
        ),
        displayElementName: link?.text
      }
    });
  }

  render(): JSX.Element {
    return (
      <Fragment>
        <div class="button-container">
          <phn-p-button
            variant="primary"
            class="login"
            onClick={() => {
              this.loginEvent.emit();
              this.sendDataLayerEvent(this.login);
            }}
          >
            {this.login.text}
          </phn-p-button>
          {this.more && isString(this.more.link) ? (
            <phn-p-link
              variant="tertiary"
              class="more"
              href={this.more.link}
              onClick={() => {
                this.sendDataLayerEvent(this.more);
              }}
            >
              {this.more.text}
            </phn-p-link>
          ) : null}
        </div>
        <div>
          <phn-p-link-pure
            icon="none"
            onClick={() => {
              this.sendDataLayerEvent(this.signup);
            }}
          >
            <a href={this.signup.link}>{this.signup.text}</a>
          </phn-p-link-pure>
          <phn-p-text>&nbsp;{this.signupText}</phn-p-text>
        </div>
      </Fragment>
    );
  }
}
